<template>
  <main class="col-md-12 mb-4">
    <div class="row">
      
      <div class="col-md-6 col-lg-4 col-xl-3" v-for="item of items" :key="item._id">
        <div class="card mb-3">
          <router-link :to="{ name: 'image', params: { id: item._id } }" exact>
            <img :src="item.image" class="card-img-top" :alt="item.title">
          </router-link>
          <!-- <div class="card-body">
            <router-link :to="{ name: 'image', params: { id: item._id } }" exact>
              <h5 class="card-title text-dark font-weight-bold mb-0">{{ item.title }}</h5>
            </router-link>
          </div> -->
          <div class="btn-group" role="group" aria-label="Basic example">
            <button type="button" class="btn btn-link btn-lg disabled text-muted"><i class="fas fa-heart"></i> {{ item.likes.length }}</button>
            <button type="button" class="btn btn-link btn-lg disabled text-muted"><i class="fas fa-comments"></i> {{ item.comment.length }}</button>
            <button type="button" class="btn btn-link btn-lg disabled text-muted"><i class="fas fa-eye"></i> {{ item.views }}</button>
          </div>
        </div>
      </div>
      
      <span></span>

      <!-- <card></card> -->
    </div>
  </main>
</template>

<script>
import { mapMutations } from 'vuex'

import Card from '@/components/Card.vue'

export default {
  name: 'home',
  components: {
    Card
  },
  data () {
    return {
      items: [],
      baseURL: 'https://photoshut-v1.herokuapp.com/api/v1',
      // baseURL: 'http://localhost:3001/api/v1',
      limit: 10,
      page: 1
    }
  },
  created () {
    this.getImages(this.limit, this.page)
  },
  methods: {
    ...mapMutations(['addLike', 'addComment', 'mostrarLoading', 'ocultarLoading', 'getLimit', 'getPage']),

    bottomVisible() {
      const scrollY = window.scrollY
      const visible = document.documentElement.clientHeight
      const pageHeight = document.documentElement.scrollHeight
      const bottomOfPage = visible + scrollY >= pageHeight
      return bottomOfPage || pageHeight < visible
    },
    async getImages (limit, page) {
      this.mostrarLoading({ titulo: 'Accediendo a información', color: 'secondary' })

      const res = await this.axios.get(`${this.baseURL}/images/limit/${limit}/page/${page}`)
      this.items = await res.data.images
    }
  },
  watch: {
    // asynchronous operations that we'll be working with in our app
  }
}
</script>

